import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@/@core/components/toastification/ToastificationContent.vue'
import UserService from '@/services/UserService'
import { reactive, ref, computed } from '@vue/composition-api'
import { localize } from 'vee-validate'
import store from '@/store/index'

const useUser = ctx => {
  const toast = useToast()
  const isSaving = ref(false)
  const isLoading = ref(false)

  const blank = {
    id: '',
    name: '',
    lastName: '',
    email: '',
    password: '',
    isActive: true,
  }

  const states = reactive({
    email: {
      isInvalid: false,
      message: '',
    },
  })
  const locale = () => {
    localize({
      en: {
        messages: {
          // generic rule messages...
        },
        fields: {
          name: {
            required: 'Introduce un nombre',
          },
          lastName: {
            required: 'Introduce los apellidos',
          },
          email: {
            required: 'El email es obligatorio',
            email: 'Introduce un email valído',
          },
          password: {
            required: 'La contraseña es obligatoria!',
          },
        },
      },
    })
  }

  const user = reactive(blank)

  const resetData = () => {
  }
  const update = async () => {
    isSaving.value = true
    try {
      const { id, ...rest } = user
      await UserService.update(user.id, rest)
      isSaving.value = false
      resetData()
      toast({
        component: ToastificationContent,
        props: {
          title: 'Actualizado Correctamente!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.email.isInvalid = true
        states.email.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Actualizar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const edit = async id => {
    isLoading.value = true
    try {
      const response = await UserService.getById(id)
      const {
        name,
        isActive,
        lastName,
        email,
      } = response.data

      user.id = response.data.id
      user.name = name
      user.isActive = isActive
      user.lastName = lastName
      user.email = email

      isLoading.value = false
    } catch (error) {
      isLoading.value = false
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Consultar perfil',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const passwordToggleIcon = computed(() => (ctx.parent.$children[0].passwordFieldType === 'password' ? 'EyeOffIcon' : 'EyeIcon'))

  const validationForm = () => {
    ctx.refs.refFormObserver.validate().then(success => {
      if (success) update()
    })
  }

  locale()

  edit(store.state.auth.user.id)

  return {
    isSaving,
    states,
    user,
    // methods
    resetData,
    edit,
    passwordToggleIcon,
    validationForm,
    isLoading,
    update,
  }
}
export default useUser
