<template>
  <b-card
    no-body
    class="mb-0"
  >
    <b-overlay
      :show="isLoading"
      spinner-variant="primary"
      spinner-type="grow"
      spinner-small
      rounded="sm"
    >
      <!-- BODY -->
      <validation-observer
        #default="{ handleSubmit }"
        ref="refFormObserver"
      >
        <b-form
          ref="form"
          class="p-2"
          @submit.prevent="handleSubmit(update)"
        >
          <b-alert
            v-if="states.email.isInvalid"
            show
            variant="danger"
          >
            <div class="alert-body">
              <feather-icon
                class="mr-25"
                icon="AlertCircleIcon"
              />
              <span class="ml-25">{{ states.email.message }}</span>
            </div>
          </b-alert>
          <b-row>
            <b-col
              cols="12"
              md="4"
            >
              <validation-provider
                #default="validationContext"
                name="name"
                rules="required"
              >
                <b-form-group
                  :state="getValidationState(validationContext)"
                  label="Nombre"
                  label-for="name"
                >
                  <b-form-input
                    id="name"
                    v-model="user.name"
                    placeholder="nombre"
                  />
                  <b-form-invalid-feedback
                    :state="getValidationState(validationContext)"
                  >
                    {{ validationContext.errors[0] }}
                  </b-form-invalid-feedback>
                </b-form-group>
              </validation-provider>
            </b-col>
            <b-col
              cols="12"
              md="4"
            >
              <validation-provider
                #default="validationContext"
                name="lastName"
                rules="required"
              >
                <b-form-group
                  :state="getValidationState(validationContext)"
                  label="Apellidos"
                  label-for="lastName"
                >
                  <b-form-input
                    id="lastName"
                    v-model="user.lastName"
                    placeholder="Apellidos"
                  />
                  <b-form-invalid-feedback
                    :state="getValidationState(validationContext)"
                  >
                    {{ validationContext.errors[0] }}
                  </b-form-invalid-feedback>
                </b-form-group>
              </validation-provider>
            </b-col>
            <b-col
              cols="12"
              md="4"
            >
              <!-- email -->
              <b-form-group
                label="Email"
                label-for="email"
              >
                <validation-provider
                  #default="{ errors }"
                  name="email"
                  rules="required|email"
                >
                  <b-form-input
                    id="login-email"
                    v-model="user.email"
                    :state="errors.length > 0 ? false:null"
                    name="email"
                    placeholder="john@example.com"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>
            <b-col
              cols="12"
              md="4"
            >
              <!-- Password -->
              <b-form-group>
                <div class="d-flex justify-content-between">
                  <label for="password">Contraseña</label>
                </div>
                <validation-provider
                  #default="{ errors }"
                  name="password"
                  :rules="user.id ? '' : 'required|min:8'"
                >
                  <b-input-group
                    class="input-group-merge"
                    :class="errors.length > 0 ? 'is-invalid' : null"
                  >
                    <b-form-input
                      id="password"
                      v-model="user.password"
                      :state="errors.length > 0 ? false : null"
                      class="form-control-merge"
                      :type="passwordFieldType"
                      name="login-password"
                      placeholder="············"
                    />
                    <b-input-group-append is-text>
                      <feather-icon
                        class="cursor-pointer"
                        :icon="passwordToggleIcon"
                        @click="togglePasswordVisibility"
                      />
                    </b-input-group-append>
                  </b-input-group>
                  <!-- <small class="text-danger">{{ errors[0] }}</small> -->
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>
            <b-col
              cols="12"
              md="4"
            >
              <b-form-group
                label="Activo"
                label-for="status"
              >
                <b-form-checkbox
                  v-model="user.isActive"
                  class="custom-control-success"
                  name="status"
                  switch
                />
              </b-form-group>
            </b-col>
          </b-row>
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mr-2"
            type="submit"
            block
          >
            <feather-icon
              icon="SaveIcon"
              class="mr-50 mt-25"
            />
            <b-spinner
              v-if="isSaving"
              small
            />
            <span class="align-middle ml-1">
              {{ user.id ? "Actualizar" : "Registrar " }}
            </span>
          </b-button>
        </b-form>
      </validation-observer>
    </b-overlay>
  </b-card>
</template>

<script>
import {
  BAlert,
  BCard,
  BRow,
  BCol,
  BFormInput,
  BOverlay,
  BForm,
  BInputGroup,
  BInputGroupAppend,
  BFormCheckbox,
  BFormGroup,
  BFormInvalidFeedback,
  BButton,
  BSpinner,
} from 'bootstrap-vue'
import { togglePasswordVisibility } from '@core/mixins/ui/forms'
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import formValidation from '@core/comp-functions/forms/form-validation'
import Ripple from 'vue-ripple-directive'
import { required, email } from '@validations'
import useProfile from './composables/useProfile'

export default {
  components: {
    BAlert,
    BCard,
    BRow,
    BCol,
    BFormInput,
    BOverlay,
    BForm,
    BInputGroup,
    BInputGroupAppend,
    BFormCheckbox,
    BFormGroup,
    BFormInvalidFeedback,
    BButton,
    BSpinner,

    // Form Validation
    ValidationProvider,
    ValidationObserver,
  },
  directives: {
    Ripple,
  },
  mixins: [togglePasswordVisibility],
  setup(__, ctx) {
    const {
      states,
      isLoading,
      user,
      isSaving,
      resetData,
      passwordToggleIcon,
      update,
    } = useProfile(ctx)

    const {
      refFormObserver,
      getValidationState,
      resetForm,
    } = formValidation(resetData)

    return {
      states,
      isLoading,
      user,
      isSaving,
      passwordToggleIcon,
      refFormObserver,
      getValidationState,
      resetForm,
      update,
      required,
      email,
    }
  },
}
</script>

<style lang="scss" scoped>

</style>
